import React, { Component } from "react"
import { Link } from "gatsby"
import { Navbar, NavbarBrand, NavbarItem, Icon, NavbarBurger, NavbarMenu, NavbarStart,
NavbarLink, NavbarDropdown, NavbarDivider, NavbarEnd, Field, Control, Button } from 'bloomer';

class NavbarApp extends Component {

  constructor(props) {
    super(props);

    this.state = {
      isActive: false
    }

    this.onClickNav = this.onClickNav.bind(this);
  }

  onClickNav(){
    this.setState({ isActive: !this.state.isActive})
  }

  render () {
    return (
      <Navbar style={{ border: 'solid 1px #00D1B2', margin: '0' }}>
        <NavbarBrand>
            <NavbarItem>
                <img src="https://bloomer.js.org/dist/07990594.png" alt="" style={{ marginRight: 5 }} /> Bloomer
            </NavbarItem>
            <NavbarItem isHidden='desktop'>
                <Icon className='fa fa-github' />
            </NavbarItem>
            <NavbarItem isHidden='desktop'>
                <Icon className='fa fa-twitter' style={{ color: '#55acee' }} />
            </NavbarItem>
            <NavbarBurger isActive={this.state.isActive} onClick={this.onClickNav} />
        </NavbarBrand>
        <NavbarMenu isActive={this.state.isActive} onClick={this.onClickNav}>
            <NavbarStart>
                <Link className="navbar-item" to="/">Home</Link>
                <NavbarItem hasDropdown isHoverable>
                    <NavbarLink>Data</NavbarLink>
                    <NavbarDropdown>
                        <Link className="navbar-item" to="/user">User</Link>
                        <NavbarDivider />
                        <Link className="navbar-item" to="/user">User Lagi</Link>
                    </NavbarDropdown>
                </NavbarItem>
            </NavbarStart>
            <NavbarEnd>
                <NavbarItem href="https://github.com/AlgusDark/bloomer" isHidden='touch'>
                    <Icon className='fa fa-github' />
                </NavbarItem>
                <NavbarItem href="https://twitter.com/AlgusDark" isHidden='touch'>
                    <Icon className='fa fa-twitter' style={{ color: '#55acee' }} />
                </NavbarItem>
                <NavbarItem>
                    <Field isGrouped>
                        <Control>
                            <Button id="twitter" data-social-network="Twitter" data-social-action="tweet"
                            data-social-target="http://bloomer.js.org" target="_blank">
                                <Icon className="fa fa-twitter" />
                                <span>Tweet</span>
                            </Button>
                        </Control>
                    </Field>
                </NavbarItem>
            </NavbarEnd>
        </NavbarMenu>
      </Navbar>
    )
  }
}


export default NavbarApp
